import React,{useState} from 'react'

export default function TextFrom(props) {
  const upCase = () =>{
    let newText = text.toUpperCase("");
    setText(newText);
  }
  const lowCase = () =>{
    let newText = text.toLowerCase("");
    setText(newText);
  }
  
  const clearText = () =>{
    let newText = '';
    setText(newText);
  }

  const copyText =() =>{
    navigator.clipboard.writeText(text);
    document.getSelection().removeAllRanges(); 
  }

  const extraSpaces = () =>{
    let newText = text.split(/[ ]+/);
    setText(newText.join(" "));
  }
    const upCaseOnchange = (event) =>{
      setText(event.target.value);
  }
  const [text, setText] = useState('');
  return (
    <>
    <div className="container">
        <h1>{props.heading}</h1>
        <div className="mb-3">
        <textarea className="form-control" value={text} onChange={upCaseOnchange} id="My Box" rows="5"></textarea>
        </div>
        <button className="btn btn-primary mx-2 my-1" onClick={upCase}>Convert to Uppercase</button>
        <button className="btn btn-primary mx-2 my-1" onClick={lowCase}>Convert to Lowercase</button>
        <button className="btn btn-primary mx-2 my-1" onClick={clearText}>Clear Text</button>
        <button className="btn btn-primary mx-2 my-1" onClick={copyText}> Copy Text</button>
        <button className="btn btn-primary mx-2 my-1" onClick={extraSpaces}>Remove Extra Spaces</button>
</div>
<div className="container">
  <h2>Your Text Summary</h2>
  <p>{text.split(/\s+/).filter((element)=>{return element.length!==0}).length} words and {text.length} characters</p>
  <h2>Preview</h2>
  <p>{text.length>0?text:"Nothing to preview!"}</p>
</div>
</>
  )
}
